import { Stack } from '@mui/material'
import React from 'react'
import { PriceLabel, RetailPrice, WeightLabel } from './priceWeightSection.styles'

const PriceWeightSection = ({ productData }) => {
  return (
    <Stack direction="row" spacing={1} justifyContent="space-between" alignItems="center">
      <WeightLabel variant="body1" align="left">
        {productData.weight}
      </WeightLabel>
      {productData.retail_price !== productData.sale_price ? (
        <>
          <RetailPrice variant="body2" align="left">
            {`$${productData.retail_price.toFixed(2)}`}
          </RetailPrice>
          <PriceLabel variant="body1" color="primary" align="right">
            {productData.sale_price
              ? `$${productData.sale_price.toFixed(2)}`
              : `$${productData.retail_price.toFixed(2)}`}
          </PriceLabel>
        </>
      ) : (
        <PriceLabel variant="body1" color="primary">
          {`$${productData.retail_price.toFixed(2)}`}
        </PriceLabel>
      )}
    </Stack>
  )
}

export default PriceWeightSection
