import { useSelector } from 'react-redux'
import { useMediaQuery } from 'react-responsive'
import FProduct from '../../../components/main/productList/festive/FProduct/fProduct.component'
import { CollectionContainer, Heading, StyledStack } from './festiveTab.styles'
import { StyledContainer } from '../homemadeTab/homemadeTab.styles'
import { ProductsContainer } from '../otherProductsTab/otherProductsTab.styles'
import NavigationList from '../../../components/common/navigationList/navigationList.component'
import CategorisedProducts from '../../../components/home/categorisedProducts/categorisedProducts.component'
import ProductList from '../../../components/common/productList/productList.component'
import CategoryNavigation from '../../../components/common/categoryNavigation/categoryNavigation.component'
import { Divider } from '@mui/material'
import { useState } from 'react'

const FestiveTab = () => {
  const { productType, brand, festive } = useSelector(state => state.products)
  const brands = brand.data || []
  // console.log('FestiveTab', festive.data.length)
  // const type = productType.data.filter(type => type.id === 'FC')[0]
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  const allTypes = []
  const allContents = []
  const [currentTab, setCurrentTab] = useState(0)
  const changeTab = (event, newValue) => {
    setCurrentTab(newValue)
  }

  productType.data.forEach((type, index) => {
    const allRelatedProducts = festive.data.filter(
      product =>
        product['product_type']?.indexOf(type.id) > -1 &&
        (product['in_stock'] || product['pre_order'] || product['coming_soon'])
    )
    if (allRelatedProducts.length > 0) {
      allTypes.push(type)
      const content = (
        <CategorisedProducts
          type={type}
          allRelatedProducts={allRelatedProducts}
          brands={brands}
          componentType={'festive'}
          key={`festive-${index}`}
        ></CategorisedProducts>
      )
      //   alltabs.push(tab)
      allContents.push(content)
    }
  })
  // console.log(allTypes.length, allContents.length)
  return (
    // <CollectionContainer>
    //   <Heading variant="h6" align="center">
    //     {type.label}
    //   </Heading>
    //   <StyledStack direction="row" useFlexGap spacing={2} alignItems={'center'} justifyContent="center">
    //     {/* <Grid container justifyContent={'center'} rowSpacing={isMobile ? 2 : 4} columnSpacing={isMobile ? 1 : 2}> */}
    //     {festive.data.map(product => {
    //       if (product['in_stock'] || product['pre_order']) {
    //         return (
    //           //   <Grid item >
    //           <FProduct
    //             key={`${product.brand} - ${product.name}`}
    //             productData={product}
    //             brands={brand.data}
    //             isFC={true}
    //           ></FProduct>
    //           //   </Grid>
    //         )
    //       }
    //       return null
    //     })}
    //     {/* </Grid> */}
    //   </StyledStack>
    // </CollectionContainer>
    <StyledContainer>
      {/* {isMobile && <CategoryNavigation allTypes={allTypes} />}
      <Divider orientation={isMobile ? 'horizontal' : 'vertical'} spacing={2} flexItem /> */}
      <ProductsContainer>
        {<NavigationList allTypes={allTypes} type={'festive'} currentTab={currentTab} onTabChange={changeTab} />}
        <ProductList allContents={allContents} type={'festive'} />
      </ProductsContainer>
    </StyledContainer>
  )
}

export default FestiveTab
