import { List, ListItemButton } from '@mui/material'
import { NavList, StyledListItem, TypeListItemText } from './navigationList.styles'

const NavigationList = ({ allTypes, type, currentTab, onTabChange }) => {
  // const [selected, setSelected] = useState(0)
  const onClickHandler = (event, typeId) => {
    event.preventDefault()
    // setSelected(allTypes.findIndex(t => t.id === typeId))
    onTabChange(
      event,
      allTypes.findIndex(t => t.id === typeId)
    )
    const element = document.getElementById(typeId)
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' })
    }
  }

  return (
    <NavList sx={{ flexGrow: 1 }} onScroll={event => event.stopPropagation()}>
      <nav aria-label="main types">
        <List>
          {allTypes.map((productType, index) => (
            <StyledListItem key={index}>
              <ListItemButton sx={{ padding: 0 }}>
                <TypeListItemText
                  onClick={event => onClickHandler(event, productType.id)}
                  primary={productType.name}
                  className={currentTab === index ? 'selected' : ''}
                  // sx={
                  //   {
                  // color: type === 'homemade' ? 'info.main' : 'info.dark'
                  // borderBottom: '0.5px solid',
                  // textDecoration: 'underline'
                  // borderColor: type === 'homemade' ? 'primary.light' : 'secondary.light'
                  // }
                  // }
                />
              </ListItemButton>
            </StyledListItem>
          ))}
        </List>
      </nav>
    </NavList>
  )
}

export default NavigationList
