export const getBadgeContent = (productData, brand) => {
  if (brand.id === 'PUR') {
    return <PurnannaImage src={homemde} />
  } else if (productData.offers?.length > 0) {
    return <OfferChip label={productData.offers[0]} color="warning" size="small" />
  } else if (!!productData.pre_order) {
    return <OfferChip label="Pre-order now" color="secondary" size="small" />
  } else if (!!productData.coming_soon) {
    return <OfferChip label="Coming soon" color="secondary" size="small" />
  } else if (productData.retail_price !== productData.sale_price) {
    return <OfferChip label="Limited time offer" color="error" size="small" />
  }
}

export const isBadgeVisible = (productData, brand) => {
  return (
    brand.id === 'PUR' ||
    productData.offers?.length > 0 ||
    productData.pre_order ||
    productData.coming_soon ||
    productData.retail_price !== productData.sale_price
  )
}
