import React, { useState } from 'react'
import { ProductCardImg, StyledCard } from './productImageCard.styles'
import ProductImage from '../../common/card/productImage/productImage.component'
import CardHeader from '../../common/card/cardHeader/cardHeader.component'
import { BrandBadge } from '../categorisedProducts/homemadeCard/homemadeCard.styles'
import ProductDetailsDialog from '../../common/ProductDetailsDialog/ProductDetailsDialog.component'
import { getBadgeContent, isBadgeVisible } from '../../common/utils/screenUtils'

const ProductImageCard = ({ type, productData, brands }) => {
  const [isOpen, setOpen] = useState(false)
  const brand = brands.filter(b => b.id === productData.brand)[0]

  return (
    <BrandBadge
      badgeContent={getBadgeContent(productData, brand)}
      invisible={!isBadgeVisible(productData, brand)}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
    >
      <StyledCard
        onClick={() => {
          setOpen(true)
        }}
        variant="outlined"
      >
        {productData.image && (
          <ProductCardImg
            image={productData.image}
            component="img"
            //   sx={{ objectFit: 'contain' }}
            title={productData.name}
          ></ProductCardImg>
        )}
        {/* {brand && <CardHeader productData={productData} brand={brand} />} */}
      </StyledCard>
      <ProductDetailsDialog
        product={{ ...productData, retail_price: type.price }}
        brand={brand}
        isOpen={isOpen}
        setOpen={setOpen}
      />
    </BrandBadge>
  )
}

export default ProductImageCard
