import { useState } from 'react'
import { useSelector } from 'react-redux'
import { useMediaQuery } from 'react-responsive'
import HomemadeAlert from '../../../components/common/homemadeAlert/homemadeAlert.component'
import NavigationList from '../../../components/common/navigationList/navigationList.component'
import ProductList from '../../../components/common/productList/productList.component'
import CategorisedProducts from '../../../components/home/categorisedProducts/categorisedProducts.component'
import { ProductsContainer, StyledContainer } from './homemadeTab.styles'

const HomemadeTab = () => {
  const { homemades, productType, brand } = useSelector(state => state.products)
  const brands = brand.data || []
  const allTypes = []
  const allContents = []
  const [currentTab, setCurrentTab] = useState(0)
  const changeTab = (event, newValue) => {
    setCurrentTab(newValue)
  }
  const isMobile = useMediaQuery({ query: '(max-width: 768px)' })
  const [scrollPosition, setScrollPosition] = useState({ scrollTop: 0 })
  const bottomPositions = {}
  // console.log(scrollPosition.scrollTop)
  const addToBottomPositions = (type, position) => {
    bottomPositions[type] = position
  }
  productType.data.forEach((type, index) => {
    const allRelatedProducts = homemades.data.filter(
      product => product['product_type']?.indexOf(type.id) > -1 && (product['in_stock'] || product['pre_order'])
    )
    if (allRelatedProducts.length > 0) {
      allTypes.push(type)
      const content = (
        <CategorisedProducts
          type={type}
          allRelatedProducts={allRelatedProducts}
          brands={brands}
          componentType={'homemade'}
          key={`homemade-${index}`}
        ></CategorisedProducts>
      )
      allContents.push(content)
    }
  })
  return (
    <StyledContainer>
      <HomemadeAlert brands={brands} />
      {/* {isMobile && <CategoryNavigation allTypes={allTypes} changeTab={changeTab} currentTab={currentTab} />} */}
      {/* <Divider orientation={isMobile ? 'horizontal' : 'vertical'} spacing={2} flexItem /> */}
      <ProductsContainer>
        {<NavigationList allTypes={allTypes} type={'homemade'} currentTab={currentTab} onTabChange={changeTab} />}
        <ProductList allContents={allContents} setScrollPosition={setScrollPosition} type={'homemade'} />
      </ProductsContainer>
    </StyledContainer>
  )
}

export default HomemadeTab
