import React from 'react'
import { useMediaQuery } from 'react-responsive'
import { CategoryName } from '../../../pages/main/main.styles'
import FestiveCard from './festiveCard/festiveCard.component'
import HomemadeCard from './homemadeCard/homemadeCard.component'
import ProductCard from './productCard/productCard.component'
import { ProductsPaper, StyledStack } from './categorisedProducts.styles'

const CategorisedProducts = ({ type, allRelatedProducts, brands, addToBottomPositions, componentType }) => {
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  const elementRef = React.useRef(null)
  return (
    <ProductsPaper id={type.id} ref={elementRef} variant="outlined">
      <CategoryName
        variant="h6"
        align="center"
        sx={{ color: componentType === 'homemade' ? 'info.dark' : 'info.dark' }}
      >
        {type.label}
      </CategoryName>

      <StyledStack direction="row" useFlexGap spacing={isMobile ? 2 : 3} alignItems={'center'} justifyContent="center">
        {allRelatedProducts.map((product, index) =>
          componentType === 'homemade' ? (
            <HomemadeCard key={`homemade-${index}`} productData={product} brands={brands}></HomemadeCard>
          ) : componentType === 'other' ? (
            <ProductCard key={`product-${index}`} productData={product} brands={brands}></ProductCard>
          ) : (
            <FestiveCard key={`festive-${index}`} productData={product} brands={brands}></FestiveCard>
          )
        )}
      </StyledStack>
    </ProductsPaper>
  )
}

export default CategorisedProducts
