import { ProductListContainer } from './productList.styles'

const ProductList = ({ allContents, setScrollPosition, type }) => {
  return (
    <ProductListContainer
      onScroll={e => setScrollPosition?.({ scrollTop: e.target.scrollTop, scrollLeft: e.target.scrollLeft })}
    >
      {allContents}
    </ProductListContainer>
  )
}

export default ProductList
