import { useState } from 'react'
import { useSelector } from 'react-redux'
import { useMediaQuery } from 'react-responsive'
import NavigationList from '../../../components/common/navigationList/navigationList.component'
import ProductList from '../../../components/common/productList/productList.component'
import CategorisedProducts from '../../../components/home/categorisedProducts/categorisedProducts.component'
import { ProductsContainer, StyledContainer } from './otherProductsTab.styles'

const OtherProductsTab = () => {
  const { product, productType, brand } = useSelector(state => state.products)
  const brands = brand.data || []
  const allTypes = []
  const allContents = []
  const [currentTab, setCurrentTab] = useState(0)
  const changeTab = (event, newValue) => {
    setCurrentTab(newValue)
  }
  const isMobile = useMediaQuery({ query: '(max-width: 768px)' })
  const [scrollPosition, setScrollPosition] = useState({ scrollTop: 0 })
  const myRefs = {}
  // console.log(scrollPosition.scrollTop)
  const addRef = (type, position) => {
    myRefs[type] = position
  }
  productType.data.forEach((type, index) => {
    const allRelatedProducts = product.data.filter(
      product =>
        product['product_type']?.indexOf(type.id) > -1 &&
        (product['in_stock'] || product['pre_order'] || product['coming_soon'])
    )
    if (allRelatedProducts.length > 0) {
      allTypes.push(type)
      const content = (
        <CategorisedProducts
          type={type}
          allRelatedProducts={allRelatedProducts}
          brands={brands}
          componentType={'other'}
          key={`other-${index}`}
        ></CategorisedProducts>
      )
      //   alltabs.push(tab)
      allContents.push(content)
    }
  })
  return (
    <StyledContainer>
      {/* {isMobile && <CategoryNavigation allTypes={allTypes} changeTab={changeTab} currentTab={currentTab} />} */}
      {/* <Divider orientation={isMobile ? 'horizontal' : 'vertical'} spacing={2} flexItem /> */}
      <ProductsContainer>
        {<NavigationList allTypes={allTypes} type={'other'} currentTab={currentTab} onTabChange={changeTab} />}
        <ProductList allContents={allContents} setScrollPosition={setScrollPosition} type={'other'} />
      </ProductsContainer>
    </StyledContainer>
  )
}

export default OtherProductsTab
