export const StyledCard = styled(Card)`
  width: 200px;
  height: 220px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  background-color: ${props => props.theme?.palette?.background?.default || '#fff'} !important;
  border-color: ${props => props.theme?.palette?.border?.paper || '#e0e0e0'} !important;
  @media (max-width: 1248px) {
    width: 180px;
  }
  @media (max-width: 1024px) {
    width: 150px;
    height: 175px;
  }
  @media (max-width: 768px) {
    width: 145px;
    height: 175px;
  }
  @media (max-width: 420px) {
    max-width: 140px;
  }
  @media (max-width: 380px) {
    max-width: 135px;
    height: 175px;
  }
  @media (max-width: 348px) {
    max-width: 125px;
    height: 175px;
  }
  .MuiCardHeader-root {
    padding: 1px;
    padding-top: 5px;
  }
  .MuiCardHeader-content {
    padding: 1px;
    padding-left: 5px;
    padding-right: 5px;
  }
  .MuiCardContent-root {
    padding-bottom: 5px;
    // padding: 5px;
  }
  .MuiCardContent-root:last-child {
    padding: 5px;
    padding-left: 5px;
    padding-right: 5px;
    @media (max-width: 768px) {
      padding-left: 5px;
      padding-right: 5px;
      padding-bottom: 1px;
    }
  }
`
