import { useSelector } from 'react-redux'
import { useMediaQuery } from 'react-responsive'
import PricedCategoryProducts from '../../../components/home/pricedCategoryProducts/pricedCategoryProducts.component'
import { StyledContainer } from '../homemadeTab/homemadeTab.styles'
import CategorisedProducts from '../../../components/home/categorisedProducts/categorisedProducts.component'

const LanternsTab = () => {
  const productTypes = ['BL', 'SL', 'TL']
  const { brand, productType, lanterns } = useSelector(state => state.products)
  console.log('LanternsTab', lanterns)
  console.log('LanternsTab productType', productType)
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  const allContents = []
  productTypes.forEach((type, index) => {
    console.log('Processing type:', type)
    const typeObj = productType.data.filter(t => t.id === type)[0]
    if (!typeObj) return
    console.log('Found type object:', typeObj)
    // const allRelatedProducts = festive.data.filter(
    const relatedProducts = lanterns.data[type]
    console.log('priced == ' + typeObj.price)
    console.log(`Related products for ${type}:`, relatedProducts)
    const content = typeObj.price ? (
      <PricedCategoryProducts
        type={typeObj}
        products={relatedProducts}
        brands={brand.data || []}
      ></PricedCategoryProducts>
    ) : (
      <CategorisedProducts
        type={typeObj}
        allRelatedProducts={relatedProducts}
        brands={brand.data || []}
        componentType={'festive'}
        key={`festive-${index}`}
      ></CategorisedProducts>
    )
    allContents.push(content)
  })
  return <StyledContainer>{allContents}</StyledContainer>
}

export default LanternsTab
