import { createSlice } from '@reduxjs/toolkit'
import { FESTIVE_PRODUCTS } from './data/festive'
import { GANESH_IDOLS } from './data/ganesha'
import { LANTERNS_PRODUCTS } from './data/lanterns'
import { BRANDS, PRODUCT_TYPES, PRODUCTS_CATEGORIES } from './data/metadata'
import { OTHER_PRODUCTS_LIST } from './data/products'
import { PURNANNA_PRODUCTS_LIST } from './data/purnanna'
const productsInitialState = {
  homemades: {
    isLoading: false,
    data: PURNANNA_PRODUCTS_LIST,
    error: false
  },
  product: {
    isLoading: false,
    data: OTHER_PRODUCTS_LIST,
    error: false
  },
  festive: {
    isLoading: false,
    data: FESTIVE_PRODUCTS,
    error: false
  },
  ganesh: {
    isLoading: false,
    data: GANESH_IDOLS,
    error: false
  },
  lanterns: {
    isLoading: false,
    data: LANTERNS_PRODUCTS,
    error: false
  },
  category: {
    isLoading: false,
    data: PRODUCTS_CATEGORIES,
    error: false
  },
  productType: {
    isLoading: false,
    data: PRODUCT_TYPES,
    error: false
  },
  brand: {
    isLoading: false,
    data: BRANDS,
    error: false
  },
  currentProduct: null
}
export const productsSlice = createSlice({
  name: 'PRODUCTS',
  initialState: productsInitialState,
  reducers: {
    /* This action will trigger our saga middleware
         and set the loader to true and reset error message.
      */
    getProductsAction: (state, payload) => {
      state.products.isLoading = true
      state.products.error = ''
    },
    getProductsSuccessAction: (state, payload) => {
      state.products.isLoading = false
      state.products.data = payload.data
    },
    getProductsErrorAction: (state, payload) => {
      state.products.isLoading = false
      state.products.error = payload.error
    }
  }
})
const { actions, reducer } = productsSlice
export const { getProductsAction, getProductsErrorAction, getProductsSuccessAction } = actions
export default reducer
