import { CardContent, CardHeader, Stack } from '@mui/material'
import { useState } from 'react'
import { useMediaQuery } from 'react-responsive'
import homemde from '../../../../assets/images/homemade.png'
import ProductDetailsDialog from '../../../common/ProductDetailsDialog/ProductDetailsDialog.component'
import {
  BrandBadge,
  CardHeaderTitle,
  PriceLabel,
  ProductCardImg,
  PurnannaImage,
  StyledCard,
  SubHeader,
  WeightLabel
} from './homemadeCard.styles'

const HomemadeCard = ({ productData, brands }) => {
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  const [isOpen, setOpen] = useState(false)
  const brand = brands.filter(b => b.id === productData.brand)[0]
  const cardHeader = (
    <CardHeader
      title={<CardHeaderTitle color={'primary'}>{productData.name}</CardHeaderTitle>}
      subheader={
        <SubHeader variant="body2" color={'secondary.dark'}>
          {brand.name}
        </SubHeader>
      }
    ></CardHeader>
  )
  const ProductImage = () => {
    return (
      <ProductCardImg
        image={`${productData.image}`}
        component="img"
        sx={{ objectFit: 'contain' }}
        title={productData.name}
      ></ProductCardImg>
    )
  }

  return (
    <BrandBadge badgeContent={<PurnannaImage src={homemde} />} anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
      <StyledCard
        // sx={{ bgcolor: brand.id === 'PUR' ? blueGrey[50] : grey[100] }}
        onClick={() => {
          setOpen(true)
        }}
        variant="outlined"
      >
        {productData.image && <ProductImage />}
        {brand && cardHeader}
        <CardContent sx={{ marginTop: 'auto', padding: '5px', marginBottom: isMobile ? '5px' : '10px' }}>
          <Stack direction="row" spacing={1} justifyContent="space-between" alignItems="center">
            <WeightLabel variant="body1" align="left">
              {productData.weight}
            </WeightLabel>
            <PriceLabel variant="body1" color="primary">
              {productData.sale_price
                ? `$ ${productData.sale_price.toFixed(2)}`
                : `$ ${productData.retail_price.toFixed(2)}`}
            </PriceLabel>
          </Stack>
        </CardContent>
      </StyledCard>
      <ProductDetailsDialog product={productData} brand={brand} isOpen={isOpen} setOpen={setOpen} />
    </BrandBadge>
  )
}

export default HomemadeCard
