import CloseIcon from '@mui/icons-material/Close'
import { DialogTitle, IconButton, Typography } from '@mui/material'
import { orange } from '@mui/material/colors'
import ShortHeader from '../ShortHeader/ShortHeader.component'
import { ProductDetailedImage, ProductImageDialog } from './PorductDetailsDialog.styles'
const ProductDetailsDialog = ({ product, isOpen = true, setOpen, brand }) => {
  return (
    <ProductImageDialog
      open={isOpen}
      color={orange[100]}
      onClose={() => {
        setOpen(false)
      }}
      sx={{ alignContent: 'center', width: '100%' }}
    >
      <DialogTitle sx={{ justifyContent: 'left' }}>
        <Typography variant="body2" color="primary" sx={{ color: 'primary', fontWeight: 'bold' }}>
          {product.name}
        </Typography>
      </DialogTitle>
      <IconButton
        aria-label="close"
        onClick={() => {
          // console.log('Dialog closed')
          setOpen(false)
        }}
        sx={theme => ({
          position: 'absolute',
          right: 8,
          top: 8,
          color: theme.palette.grey[500]
        })}
      >
        <CloseIcon />
      </IconButton>

      <ShortHeader product={product} brand={brand}></ShortHeader>
      <div style={{ display: 'flex', justifyContent: 'center', padding: 2 }}>
        <ProductDetailedImage src={`${product.image}`} height={'450px'}></ProductDetailedImage>
      </div>
    </ProductImageDialog>
  )
}

export default ProductDetailsDialog
