import { CardContent } from '@mui/material'
import { useState } from 'react'
import { useMediaQuery } from 'react-responsive'
import CardHeader from '../../../common/card/cardHeader/cardHeader.component'
import PriceWeightSection from '../../../common/card/priceWeightSection/priceWeightSection.component'
import ProductImage from '../../../common/card/productImage/productImage.component'
import ProductDetailsDialog from '../../../common/ProductDetailsDialog/ProductDetailsDialog.component'
import { getBadgeContent, isBadgeVisible } from '../../../common/utils/screenUtils'
import { BrandBadge, StyledCard } from './productCard.styles'

const ProductCard = ({ productData, brands }) => {
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  const [isOpen, setOpen] = useState(false)

  const brand = brands.filter(b => b.id === productData.brand)[0]

  return (
    <BrandBadge
      invisible={!isBadgeVisible(productData, brand)}
      badgeContent={getBadgeContent(productData, brand)}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
    >
      <StyledCard
        onClick={() => {
          setOpen(true)
        }}
        variant="outlined"
      >
        {productData.image && <ProductImage image={productData.image} title={productData.name} />}
        {brand && <CardHeader productData={productData} brand={brand} />}
        <CardContent sx={{ marginTop: 'auto', padding: '5px', marginBottom: isMobile ? '5px' : '10px' }}>
          <PriceWeightSection productData={productData} />
        </CardContent>
      </StyledCard>
      <ProductDetailsDialog product={productData} brand={brand} isOpen={isOpen} setOpen={setOpen} />
    </BrandBadge>
  )
}

export default ProductCard
