import { Grid, Typography } from '@mui/material'
import { PriceLabel, WeightChip } from '../../main/productList/product/product.styles'

const ShortHeader = ({ product, brand }) => {
  const price =
    product.sale_price && product.sale_price !== product.retail_price ? product.sale_price : product.retail_price

  return (
    <Grid
      container
      //   columnSpacing={3}
      sx={{
        justifyContent: 'center',
        alignItems: 'center',
        margin: 1
      }}
    >
      <Grid size={{ xs: 5 }}>
        <Typography variant="body2" color="secondary" sx={{ fontWeight: 'bold', paddingLeft: 2, paddingBottom: 1 }}>
          Brand: {brand.name}
        </Typography>
      </Grid>
      {product.weight && (
        <Grid size={{ xs: 3 }} offset={{ xs: 1 }}>
          <WeightChip label={product.weight} size="small" color="success" />
        </Grid>
      )}
      {price && typeof price === 'number' ? (
        <Grid size={{ xs: 2 }} offset={{ xs: 1 }}>
          <PriceLabel variant="body1" color="success">{`$ ${price.toFixed(2)}`}</PriceLabel>
        </Grid>
      ) : (
        price && (
          <Grid size={{ xs: 2 }} offset={{ xs: 1 }}>
            <PriceLabel variant="body1" color="success">{`$ ${product.retail_price}`}</PriceLabel>
          </Grid>
        )
      )}
    </Grid>
  )
}

export default ShortHeader
